.btn {
  display: inline-block;
  padding: 10px 16px;
  min-width: 64px;
  font-family: $font-family-os;
  font-size: 15px;
  font-weight: 500;
  color: #fff;
  letter-spacing: .15px;
  background: #B71C1C;
  text-decoration: none;
  //text-transform: uppercase;
  text-align: center;
  border-radius: 4px;
  border: 0;
  outline: none;
  cursor: pointer;
  user-select: none;
  transition: all .2s ease-in-out;

  // Версия для слабовидящих
  #{$body-class} & {
    border: 2px dotted #000000;
  }

  &:hover {
    box-shadow: 0 0 2px rgba(0,0,0,.12), 0 2px 2px rgba(0,0,0,.2);
  }

  &:active {
    background: #D32F2F;
    box-shadow: 0 0 4px rgba(0,0,0,.12), 1px 3px 4px rgba(0,0,0,.2);
  }

  &_link {
    background: transparent;
    color: #54555e;

    &:hover {
      color: #B71C1C;
      box-shadow: none;
    }

    &:active {
      background: #fff;
      box-shadow: none;
    }

  }

  &_outline {
    background: transparent;
    border: 1px solid rgba(255,255,255,.8);

    &:hover {
      color: #36373f;
      background: #fff;
    }

    &:active {
      background: #fff;
    }

  }

  &_sm {
    padding: 5px 10px;
    min-width: 30px;
    font-size: 12px
  }

  &_gray {
    color: #171821;
    background: #F5F5F6;

    &:hover {
      color: #171821;
      background: #F5F5F6;
    }

    &:active {
      background: #F5F5F6;
    }

  }

}

@media only screen and (max-width : 1024px) {

  .btn {
    padding: 8px 12px;
    //min-width: 64px;
    //font-family: $font-family-os;
    font-size: 14px;
  }

}

