.card {
  display: block;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 1px 0 rgba(60,64,67,.08), 0 1px 3px 1px rgba(60,64,67,.16);
  text-decoration: none;

  &_of {
    overflow: hidden;
  }

  &_h {
    height: 100%;
    min-height: 355px;
  }

  &_mt-5 {
    margin-top: 50px;
  }

  &_mb {
    margin-bottom: 50px;
  }

  &_bl-1 {
    border-left: 4px solid #B71C1C;
  }

  &_bl-2 {
    border-left: 4px solid #880E4F;
  }

  &_bl-3 {
    border-left: 4px solid #4A148C;
  }

  &_bl-4 {
    border-left: 4px solid #D50000;
  }

  &_bl-5 {
    border-left: 4px solid #C51162;
  }

  &_bl-6 {
    border-left: 4px solid #AA00FF;
  }

  &_bl-7 {
    border-left: 4px solid #311B92;
  }

  &_bl-8 {
    border-left: 4px solid #1A237E;
  }

  &_bl-9 {
    border-left: 4px solid #0D47A1;
  }

  &_bl-10 {
    border-left: 4px solid #01579B;
  }

  &_bl-11 {
    border-left: 4px solid #006064;
  }

  &_bl-12 {
    border-left: 4px solid #F57F17;
  }

  &_bl-13 {
    border-left: 4px solid #df2c2c;
  }

  &_link {

    &:hover .card-title {
      color: #B71C1C;
    }

  }

}

.card-header {
  display: flex;
  align-items: flex-end;
}


.card-body {
  padding: 20px;
}

.card-title {
  margin: 0;
  //padding-top: 10px;
  font-family: $font-family-m;
  font-size: 16px;
  font-weight: 600;
  color: #171821;
  text-transform: uppercase;
  line-height: 1;
  transition: color .2s ease-in-out;

  &_td-lc {
    text-transform: none;
    line-height: 1.5;
  }

  &_question {
    margin: 20px 0;
    color: #54555e;
    font-weight: 500;
  }

  &__link {
    display: block;
    color: #171821;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #B71C1C;
    }

  }

}

.card-header-nav {
  display: flex;
  margin: 0 0 0 auto;
  padding: 0;
  list-style: none;

  &_left {
    margin: 0 0 0 40px;
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-family: $font-family-m;
    font-size: 14px;
    font-weight: 600;
    color: #B71C1C;
    text-transform: uppercase;
    text-decoration: none;

    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .8;
    }

  }

}


.card-detail {
  font-family: $font-family-os;
  font-size: 14px;
  font-weight: 400;
  color: #54555e;
  line-height: 1.5;
}

.card-time {
  display: block;
  margin: 0;
  padding-top: 20px;
  font-family: $font-family-m;
  font-size: 12px;
  font-weight: 500;
  color: #54555e;
  text-transform: uppercase;
}

.card-nav {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }

  }

}

.card-img-box {
  position: relative;
  height: 200px;
  overflow: hidden;

  &__img {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    //min-height: 100%;
  }

}

.card-link {

}

.card-list {
  display: flex;
  margin: 0 -5px;
  flex-wrap: wrap;

  &_2 &__item {
    width: 50% !important;
  }

  &_link &__item {
    width: auto !important;
  }

  &_4 &__item {
    width: 25% !important;
  }

  &__item {
    width: 100%;
    padding: 5px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .card {

    &_h {
      min-height: 285px;
    }

    &_mt-5 {
      margin-top: 40px;
    }

    &_mb {
      margin-bottom: 40px;
    }

  }

  .card-header {
    display: flex;
    align-items: flex-end;
  }


  .card-body {
    padding: 20px;
  }

  .card-title {
    font-size: 16px;

    &_td-lc {
      font-size: 14px;
    }

    &_question {
      margin: 20px 0;
      font-size: 14px;
    }

    &__link {
      display: block;
      color: #171821;
      text-decoration: none;
      transition: color .2s ease-in-out;

      &:hover {
        color: #B71C1C;
      }

    }

  }

  .card-header-nav {

    &_left {
      margin: 0 0 0 40px;
    }

    &__item {
      margin-right: 10px;
    }

    &__link {
      font-size: 12px;
    }

  }


  .card-detail {
    font-size: 14px;
  }

  .card-time {
    padding-top: 15px;
    font-size: 11px;
  }

  .card-nav {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;

    &__item {
      margin-right: 20px;

      &:last-child {
        margin-right: 0;
      }

    }

  }

  .card-img-box {
    height: 155px;

    &__img {

    }

  }

  .card-link {

  }

  .card-list {
    display: flex;
    margin: 0 -5px;
    flex-wrap: wrap;

    &_2 &__item {
      width: 50% !important;
    }

    &_link &__item {
      width: auto !important;
    }

    &_4 &__item {
      width: 25% !important;
    }

    &__item {
      width: 100%;
      padding: 5px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .card {

    &_h {
      min-height: 264px;
    }

    &_mt-5 {
      margin-top: 40px;
    }

    &_mb {
      margin-bottom: 40px;
    }

  }

  .card-list {
    display: flex;
    margin: 0 -5px;
    flex-wrap: wrap;

    &_2 &__item {
      width: 50% !important;
    }

    &_link &__item {
      width: auto !important;
    }

    &_4 &__item {
      width: 50% !important;
    }

    &__item {
      width: 100%;
      padding: 5px;
    }

  }

  .card-header-nav {

    &__item {
      margin-top: 5px;
      margin-right: 0;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {


  .card {

    &_h {
      min-height: auto;
    }

    &_mt-5 {
      margin-top: 40px;
    }

    &_mb {
      margin-bottom: 20px;
    }

  }

  .card-header {
    align-items: flex-end;
    flex-wrap: wrap;

    & > * {
      width: 100%;
    }

  }

  .card-nav {

    &__item {
      margin-right: 10px;
    }

  }

  .card-list {

    &_2 &__item {
      width: 100% !important;
    }

    &_link &__item {
      width: 100% !important;
    }

    &_4 &__item {
      width: 100% !important;
    }

    &__item {
      //width: 100%;
      //padding: 5px;
    }

  }

}

