.img-grid {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin: 20px -5px;

  &_2 {

    .img-grid {

      &__item {
        width:50%!important;
      }

    }

  }

  &_3 {

    .img-grid {

      &__item {
        width: 33.33%!important;
      }

    }

  }

  &_4 {

    .img-grid {

      &__item {
        width: 25%!important;
      }

    }

  }

  &__item {
    width: 100%;
    padding: 5px;

    & > a {
      display: inline-block;
      width: 100%;
      vertical-align: top;
      text-decoration: none;

      & > img {
        width: 100%;
        margin: 0;
        vertical-align: top;
      }

    }

    & p {
      position: relative;
      margin: 10px 0;
      font-size: 14px;
      font-weight: 600;
      color: rgba(0,0,0,.6);
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .img-grid {

    &_3 {

      .img-grid {

        &__item {
          width: 50%!important;
        }

      }

    }

    &_4 {

      .img-grid {

        &__item {
          width: 50%!important;
        }

      }

    }

    &__item {

      & p {
        margin: 10px 0 30px 0;
        font-size: 14px;
      }

    }


  }

}

