.footer {
  width: 100%;
  padding-bottom: 50px;
  background: #171821;

  &__row {
    padding-top: 50px;
    margin: 0 -20px;
    display: flex;
  }

  &__col {

    &-1-2 {
      padding: 0 20px;
      width: 50%;
    }

    &-1-3 {
      padding: 0 20px;
      width: 33.33%;
    }

    &-1-4 {
      padding: 0 20px;
      width: 25%;
    }

  }

}

.footer-menu-title {
  margin: 0 0 20px 0;
  font-family: $font-family-m;
  font-size: 14px;
  font-weight: 600;
  color: #fff;
  text-transform: uppercase;
}

.footer-menu {
  margin: 0;
  padding: 0;
  list-style: none;

  &__item {
    margin-bottom: 5px;

    &:last-child{
      margin-bottom: 0;
    }

  }

  &__link {
    font-family: $font-family-os;
    font-size: 14px;
    font-weight: 400;
    color: #83858f;
    line-height: 1.5;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #fff;
    }

    &_phone {
      position: relative;
      padding-left: 25px;

      // Версия для слабовидящих
      #{$body-class} & {
        padding-left: 0;

        &:before {
          content: none;
        }

      }

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 2px;
        background: url("../images/contact/phone.svg") no-repeat;
        background-size: 15px 15px;
        width: 15px;
        height: 15px;
      }

    }

    &_envelope {
      position: relative;
      padding-left: 25px;

      // Версия для слабовидящих
      #{$body-class} & {
        padding-left: 0;

        &:before {
          content: none;
        }

      }

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 2px;
        background: url("../images/contact/envelope.svg") no-repeat;
        background-size: 15px 15px;
        width: 15px;
        height: 15px;
      }

    }

    &_map {
      position: relative;
      padding-left: 25px;

      // Версия для слабовидящих
      #{$body-class} & {
        padding-left: 0;

        &:before {
          content: none;
        }

      }

      &:before {
        content: '';
        position: absolute;
        left: 0;
        top: 2px;
        background: url("../images/contact/map.svg") no-repeat;
        background-size: 15px 15px;
        width: 15px;
        height: 15px;
      }

    }

  }

}

.footer-text {
  margin: 0;
  font-family: $font-family-os;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
  line-height: 1.5;
  text-decoration: none;
  transition: color .2s ease-in-out;

  & a {
    color: #83858f;
    transition: color .2s ease-in-out;
    text-decoration: none;

    &:hover {
      color: #fff;
    }
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .footer {

    &__row {
      padding-top: 0;
      flex-wrap: wrap;
    }

    &__col {

      &-1-2 {
        padding: 40px 20px 0;
        width: 50%;
      }

      &-1-3 {
        padding: 40px 20px 0;
        width: 50%;
      }

      &-1-4 {
        padding: 40px 20px 0;
        width: 50%;
      }

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .footer {

    &__row {
      padding-top: 0;
      flex-wrap: wrap;
    }

    &__col {

      &-1-2 {
        padding: 40px 20px 0;
        width: 100%;
      }

      &-1-3 {
        padding: 40px 20px 0;
        width: 100%;
      }

      &-1-4 {
        padding: 40px 20px 0;
        width: 100%;
      }

    }

  }

}

