.slider {

  &__item {
    padding-bottom: 40px;
    display: none;
    outline: none;
  }

  &__title {
    font-family: $font-family-m;
    font-size: 36px;
    font-weight: 400;
    color: #fff;
  }

  &__teaser {
    padding-top: 40px;
    font-family: $font-family-m;
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    line-height: 1.5;
  }

  &__link-wrap {
    padding-top: 20px;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .slider {

    &__item {
      padding-bottom: 20px;
    }

    &__title {
      font-size: 28px;
    }

    &__teaser {
      padding-top: 20px;
      font-size: 14px;
    }

    &__link-wrap {

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .slider {

    &__item {
      padding-bottom: 20px;
    }

    &__title {
      font-size: 24px;
      line-height: 1.5;
    }

    &__teaser {
      padding-top: 10px;
      font-size: 14px;
    }

    &__link-wrap {

    }

  }

}

