.search-main {

  &__input-wrap {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 8px 0 0;
    height: auto;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 1px 0 rgba(60,64,67,.08), 0 1px 3px 1px rgba(60,64,67,.16);

    // Версия для слабовидящих
    #{$body-class} & {
      border: 2px solid #000000;
    }

  }

  &__select {
    width: 140px;
    padding: 0 0 0 20px;
    font-family: $font-family-os;
    font-size: 16px;
    font-weight: 400;
    color: #36373f;
    background: transparent;
    border: none;
    cursor: pointer;
    outline: 0;

    & option[selected] {
      color: #36373f;
    }

  }

  &__input {
    width: 100%;
    flex-grow: 1;
    padding: 10px 10px 10px 20px;
    background: none;
    font-family: $font-family-os;
    font-size: 16px;
    font-weight: 400;
    color: #36373f;
    border: none;
    //width: 58%;
    outline: 0;

    &_plh-blue {

      &::-webkit-input-placeholder {color:#54555e;}
      &::-moz-placeholder          {color:#54555e;}/* Firefox 19+ */
      &:-moz-placeholder           {color:#54555e;}/* Firefox 18- */
      &:-ms-input-placeholder      {color:#54555e;}

    }

  }

  &__btn {
    padding: 0 20px;
    height: 20px;
    background-color: transparent;
    background: url("../images/search.svg") no-repeat;
    background-size: 20px 20px;
    background-position: center;
    border: 0;
    outline: none;
    cursor: pointer;
  }

  &__side-detail-nav {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 10px 20px;
    list-style: none;
  }

  &__side-detail-nav-item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__crumbs {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 20px;
    list-style: none;
  }

  &__crumbs-item {
    position: relative;
    margin-right: 25px;
    margin-bottom: 5px;

    &:after {
      content: '→';
      position: absolute;
      top: 0;
      right: -20px;
      color: #777;
    }

    //&:first-child {
    //
    //  &:after {
    //    content: none;
    //  }
    //
    //}

    &:last-child {

      &:after {
        content: none;
      }

    }

    &_crumbs-clear-btn {
      margin-bottom: 13px;
    }

  }

  &__crumbs-text-link {
    font-family: $font-family-os;
    font-size: 13px;
    font-weight: 400;
    color: #B71C1C;
    text-decoration: none;
    transition: opacity .2s;
    line-height: 16px;

    &:hover {
      opacity: .5;
    }
  }

  &__crumbs-text {
    font-family: $font-family-os;
    font-size: 13px;
    font-weight: 400;
    color: #333;
    text-decoration: none;
    transition: opacity .2s;
    line-height: 16px;
  }

  &__nav {
    display: flex;
    //align-items: center;
    margin: 20px 0 0 0;
    padding: 0;
    list-style: none;
  }

  &__nav-item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__nav-link {
    font-family: $font-family-os;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: rgba(255,255,255,.7);
    }

  }

  &__nav-link-sm {
    font-family: $font-family-os;
    font-size: 14px;
    font-weight: 500;
    color: #54555e;
    text-decoration: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #B71C1C;
    }

    &_active {
      font-weight: 600;
    }

  }

  &__nav-text {
    font-family: $font-family-os;
    font-size: 14px;
    color: #54555e;
    line-height: 1.3;
  }

  &__nav-select-wrap {
    font-size: 14px;
    color: #54555e;
  }

  &__nav-select {
    background: #fff;
    border: 0;
    border-radius: 4px;

  }

  &__side-wrap {
    padding: 10px 0;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 1px 0 rgba(60,64,67,.08), 0 1px 3px 1px rgba(60,64,67,.16);
  }

}

.search-results {
  padding-left: 50px;
}

.search-results-item {
  display: flex;
  margin-top: 10px;
  padding: 20px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 1px 0 rgba(60,64,67,.08), 0 1px 3px 1px rgba(60,64,67,.16);

  &__number {
    padding: 4px 10px 0 0;
    font-family: $font-family-os;
    font-size: 12px;
    font-weight: 300;
    color: #54555e;
  }

  &__description {

  }

  &__title {
    margin: 0 0 10px 0;
    font-family: $font-family-os;
    font-size: 18px;
    font-weight: 400;
    //color: rgba(0,0,0,0.87);
    line-height: normal;

    & p {
      margin: 0;
    }

  }

  &__title-link {
    color: #B71C1C;
    text-decoration: none;
    transition: opacity .2s;

    &:hover {
      opacity: .5;
    }

  }

  &__tb {
    display: table;
    border-spacing: 0 5px;
    font-family: $font-family-os;
    font-size: 12px;
    font-weight: 400;
    color: #54555e;
    line-height: 1.3;
  }

  &__tb-rw {
    display: table-row;
    margin-top: 10px;
  }

  &__tb-cl {
    display: table-cell;

    &:first-child {
      padding-right: 10px;
      white-space: nowrap;
    }

  }

  & a {
    color: #B71C1C;
    text-decoration: none;
    transition: opacity .2s;

    &:hover {
      opacity: .5;
    }
  }

}

.search-detail {
  padding-left: 50px;

  &__text {
    font-family: $font-family-os;
    font-size: 14px;
    font-weight: 400;
    color: #777;
    line-height: 22px;
  }

}

