/*
	list Styler
*/

#list, #list ul, #list li {
  margin: 0;
  padding: 0;
  list-style: none;
}

#list {
  background: url('../images/list/line1.gif') repeat-y;
}

#list li {
  //line-height: 20px;
  margin-top: 1px;
  position: relative;
  width: 100%;
}

/* IE leaves a blank space where span is added so this is to avoid that */
* html #list li {
  float: left;
  display: inline;
}

#list li a {
  display: inline-block;
  padding-left: 28px;
}

#list li span {
  float: left;
  position: absolute;
  top: 5px;
  left: 5px;
  width: 13px;
  height: 13px;
  cursor: auto;
  font-size: 0;
  cursor: pointer;
}

#list li span, #list li span.collapsed {
  background: url('../images/list/collapsed.gif') no-repeat 0 0;
}

#list li span.expanded {
  background: url('../images/list/expanded.gif') no-repeat 0 0;
}

/* sub levels */

#list li ul {
  margin-left: 28px;
  background: url('../images/list/line1.gif') repeat-y;
}

#list li li {
  background: url('../images/list/line2.gif') no-repeat 0 0;
}

/* etc. */

/* float fix */

#list:after, #list ul:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

#list, #list ul {
  display: block;
}

/*  \*/
#list, #list ul {
  min-height: 1%;
}

* html #list, * html #list ul {
  height: 1%;
}

/*  */
#list ul {
  display: none;
}
