/*======================================
  Selectric v1.13.0
======================================*/

.selectric-wrapper {
    position: relative;
    cursor: pointer;
}

.selectric-responsive {
    width: 100%;
}

.selectric {
    border: solid 2px #f5f5f5;
    border-radius: 4px;
    background: #fff;
    position: relative;
    overflow: hidden;
}

.has-error .selectric {
    border-color: #ec4d5c!important;
}

.has-success .selectric {
    border-color: #3c763d!important;
}

.selectric .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    /*margin: 0 38px 0 10px;*/
    padding: 10px 9px;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    font-weight: 500;
    text-align: left;
    line-height: 1.3;
    color: #171821;
    height: 38px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.selectric .button {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 38px;
    height: 38px;
    line-height: 38px;
    /*background-color: #fff;*/
    color: #fff;
    text-align: center;
    /*font: 0/0 a;*/
    /**font: 20px/38px Lucida Sans Unicode, Arial Unicode MS, Arial;*/
}

.selectric .button:after {
    content: " ";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 0;
    height: 0;
    border: 4px solid transparent;
    border-top-color: #BBB;
    /*border-top-color: transparent;*/
    border-bottom: none;
}

.selectric-focus .selectric {
    border-color: #f5f5f5;
}

.selectric-hover .selectric {
    border-color: #c4c4c4;
}

.selectric-hover .selectric .button {
    color: #a2a2a2;
}

.selectric-hover .selectric .button:after {
    border-top-color: #a2a2a2;
}

.selectric-open {
    z-index: 9999;
}

.selectric-open .selectric {
    border-color: #54555e;
}

.selectric-open .selectric-items {
    display: block;
}

.selectric-disabled {
    filter: alpha(opacity=50);
    opacity: 0.5;
    cursor: default;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.selectric-hide-select {
    position: relative;
    overflow: hidden;
    width: 0;
    height: 0;
}

.selectric-hide-select select {
    position: absolute;
    left: -100%;
}

.selectric-hide-select.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
}

.selectric-hide-select.selectric-is-native select {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    border: none;
    z-index: 1;
    box-sizing: border-box;
    opacity: 0;
}

.selectric-input {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    overflow: hidden !important;
    clip: rect(0, 0, 0, 0) !important;
    margin: 0 !important;
    padding: 0 !important;
    width: 1px !important;
    height: 1px !important;
    outline: none !important;
    border: none !important;
    *font: 0/0 a !important;
    background: none !important;
}

.selectric-temp-show {
    position: absolute !important;
    visibility: hidden !important;
    display: block !important;
}

/* Items box */
.selectric-items {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background: #fff;
    border: 2px solid #f5f5f5;
    border-radius: 4px;
    z-index: -1;
    /*box-shadow: 0 0 10px -6px;*/
    box-shadow: 0 8px 32px 0 rgba(0,0,0,.2);
}

.selectric-items .selectric-scroll {
    height: 100%;
    overflow: auto;
}

.selectric-above .selectric-items {
    top: auto;
    bottom: 100%;
}

.selectric-items ul, .selectric-items li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    min-height: 20px;
}

.selectric-items li {
    display: block;
    padding: 10px;
    color: #b3b3b3;
    cursor: pointer;
}

.selectric-items li.selected {
    background: #f5f5f5;
    color: #171821;
}

.selectric-items li.highlighted {
    /*background: #D0D0D0;*/
    color: #444;
}

.selectric-items li:hover {
    /*background: #D5D5D5;*/
    color: #171821;
}

.selectric-items .disabled {
    filter: alpha(opacity=50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.selectric-items .selectric-group .selectric-group-label {
    font-weight: bold;
    padding-left: 10px;
    cursor: default;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background: none;
    color: #444;
}

.selectric-items .selectric-group.disabled li {
    filter: alpha(opacity=100);
    opacity: 1;
}

.selectric-items .selectric-group li {
    padding-left: 25px;
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

    .selectric .label {
        padding: 7px 9px;
        height: 32px;
    }

    .selectric .button {
        width: 32px;
        height: 32px;
        line-height: 32px;
    }

    .selectric .button:after {
        content: " ";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: auto;
        width: 0;
        height: 0;
        border: 4px solid transparent;
        border-top-color: #BBB;
        /*border-top-color: transparent;*/
        border-bottom: none;
    }

}

