.record-search {

  .record {
    margin-top: 0;
    font-size: 14px;
    font-weight: 400;
    color: #54555e;
    line-height: 1.3;
  }


}

.record {
  margin-top: 30px;
  font-family: $font-family-os;
  font-size: 18px;
  font-weight: 400;
  color: #333;
  line-height: 28px;

  & a {
    color: #B71C1C;
    text-decoration: none;
    transition: opacity .2s;

    &:hover {
      opacity: .5;
    }

  }

}
