.calendar {
  width: 100%;
  //margin: 20px 0 0 0;
  text-align: center;
  font-family: $font-family-m;
  font-weight: 600;
}
.calendar a {
  font-size: 14px;
  color: #54555e;
  text-decoration: none;
}
.calendar td, .calendar th {
  //width: 40px;
  //width: 100%;
  height: 38px;
}
.calendar .days-week {
  //background: #F5F5F6;
  //border-radius: 4px;
}

.calendar .days-week th {
  color: #54555e;
  font-size: 14px;
  font-weight: 600;
}

.calendar .evnt {
  display: block;
  //width: 34px;
  //width: 100%;
  font-weight: 600;
  line-height: 34px;
  background: #F5F5F6;
  color: #B71C1C;
  border-radius: 4px;
  transition: opacity .2s ease-in-out;

  &:hover {
    opacity: .7;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .calendar {

  }

  .calendar a {
    font-size: 12px;
  }

  .calendar td, .calendar th {
    height: 32px;
  }
  .calendar .days-week {

  }

  .calendar .days-week th {
    font-size: 12px;
  }

  .calendar .evnt {
    line-height: 32px;
  }

}

