.facets-item {
  //padding: 15px 20px 0 20px;

  &:first-child {
    padding-top: 0;
  }

  &__title-wrap {
    padding: 10px 20px;
    cursor: pointer;
  }

  &__title-wrap_active {

  }

  &__title {
    margin: 0;
    font-family: $font-family-os;
    font-size: 18px;
    font-weight: 400;
    color: #171821;
  }

  //&__title-wrap:hover &__title {
  //  color: #5cb85c;
  //}

  &__panel {
    //display: none;
    padding: 10px 20px;
    margin-bottom: 10px;

    &_plr0 {
      padding: 10px 0;
    }

  }

  &__nav-btn {
    padding-top: 10px;
  }

  &__nav {
    margin: 0;
    padding: 0;
    list-style: none;
    line-height: 16px;
  }

  &__nav-item {
    padding-bottom: 5px;
  }

  &__nav-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: $font-family-os;
    font-size: 13px;
    font-weight: 400;
    color: #54555e;
    text-decoration: none;
    transition: color .2s;

    &:hover {
      color: #B71C1C;
    }

  }

  &__nav-item-name {
    padding-right: 5px;
  }

  &__nav-item-count {
    display: inline-block;
    align-self: flex-start;
    padding: 2px 10px;
    font-size: 13px;
    font-weight: 400;
    color: #777;
    white-space: nowrap;
    border-radius: 25px;
    //background: rgba(48,95,129,0.05);
  }

}

