.logo-wrap {
  max-width: 290px;

  // Версия для слабовидящих
  #{$body-class} & {
    max-width: 800px;
  }

}

.logo {
  display: flex;
  text-decoration: none;

  &__img-wrap {
    position: relative;

    &:after {
      content: '120 лет';
      position: absolute;
      z-index: 2;
      left: 0;
      bottom: -15px;
      width: 62px;
      font-family: $font-family-m;
      font-weight: 600;
      font-size: 15px;
      color: #B71C1C;
      text-transform: uppercase;
      text-align: center;
    }

  }

  &__img {
    width: 60px;
    height: auto;
    max-height: 71px;
    vertical-align: top;
  }

  &__text-wrap {
    padding-left: 10px;
  }

  &__text-t {
    font-family: $font-family-m;
    font-weight: 500;
    font-size: 13px;
    color: #171821;
    text-transform: uppercase;
  }

  &__text-b {
    padding-top: 10px;
    font-family: $font-family-m;
    font-weight: 500;
    font-size: 10px;
    color: #83858f;
    text-transform: uppercase;
  }

  &__text-mob {
    display: none;
    font-family: $font-family-m;
    font-weight: 500;
    font-size: 20px;
    color: #171821;
    text-transform: uppercase;
  }

}

@media only screen and (max-width : 1024px) {

  .logo-wrap {
    max-width: 290px;
  }

  .logo {

    &__img-wrap {

      &:after {
        content: '120 лет';
        bottom: -15px;
        font-size: 12px;
        width: 50px;
      }

    }

    &__img {
      width: 50px;
    }

    &__text-wrap {

    }

    &__text-t {
      font-size: 11px;
    }

    &__text-b {
      padding-top: 6px;
      font-size: 9px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .logo {

    &__img-wrap {

      &:after {
        content: '';
      }

    }

    &__img {
      width: 30px;
    }

    &__text-wrap {
      display: flex;
      align-items: center;
    }

    &__text-t {
      display: none;
    }

    &__text-b {
      display: none;
    }

    &__text-mob {
      display: block;
    }

  }


}

