.crumb {
  display: flex;
  padding: 20px 0;
  font-family: $font-family-m;
  font-size: 12px;

  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
    font-weight: 400;

    & > li {
      position: relative;
      padding: 0 10px;

      &:after {
        content: "/";
        font-size: 14px;
        color: rgba(84, 85, 94, 0.2);
        vertical-align: middle;
        position: absolute;
        top: -3px;
        right: -5px;
      }

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        &:after {
          content: "";
        }
      }

      & > span {
        white-space: nowrap;
      }

      & > a {
        color: #acaeb4;
        text-decoration: none;
        transition: all .2s;
        white-space: nowrap;

        &:hover {
          color: #B71C1C;
        }

      }

    }

  }

  &__current {
    color: #acaeb4;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .crumb {
    overflow: auto;
  }

}

/* Small Devices, Tablets */
