.section {
  padding-top: 50px;

  &__title {
    margin: 0;
    font-family: $font-family-m;
    font-size: 21px;
    font-weight: 600;
    color: #171821;
    text-transform: uppercase;
    line-height: 1;
  }

  &__divider {
    margin-bottom: 50px;
    width: 100%;
    height: 3px;
    background: #888994;
  }

}

.section-header {
  display: flex;
  align-items: flex-end;
  padding-bottom: 40px;
}

.section-nav {
  display: flex;
  margin: 0 0 0 auto;
  padding: 0;
  list-style: none;

  &_left {
    margin: 0 0 0 40px;
  }

  &__item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-family: $font-family-m;
    font-size: 14px;
    font-weight: 600;
    color: #B71C1C;
    text-transform: uppercase;
    text-decoration: none;

    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .8;
    }

    &_rss {
      color: #e65100;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .section {
    padding-top: 40px;

    &__title {
      font-size: 16px;
      font-weight: 600;
    }

    &__divider {
      margin-bottom: 40px;
    }

  }

  .section-header {
    padding-bottom: 30px;
  }

  .section-nav {

    &_left {
      margin: 0 0 0 40px;
    }

    &__item {
      margin-right: 10px;
    }

    &__link {
      font-size: 12px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .section-nav {

    &_left {
      margin: 0 0 0 auto;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .section {

    &__title {
      line-height: 1.3;
    }

  }

  .section-nav {
    flex-wrap: wrap;

    &_left {
      //margin: 0 0 0 20px;
    }

    &__item {
      width: 100%;
      margin-right: 0;
      text-align: right;
    }

    &__link {
      white-space: nowrap;
