.menu-nav {
  padding-top: 15px;
  position: relative;
}

.menu-nav-user {
  display: none;
}

.menu {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  background: #fff;

  // Версия для слабовидящих
  #{$body-class} & {
    flex-wrap: wrap;
  }

  &__item {
    transition: 1s ease-out;
    transition-delay: 1s;

    &:hover > a, &:hover > div > a {
      color: #B71C1C;
    }

    &:hover .menu-sub-container {
      visibility: visible;
      transition-delay: .2s;
    }

  }

  &__link {
    display: inline-block;
    padding: 15px 10px;
    font-family: $font-family-os;
    font-size: 15px;
    font-weight: 600;
    color: #36373f;
    text-decoration: none;
    user-select: none;
    transition: color .2s ease-in-out;

    &:hover {
      color: #B71C1C;
    }

  }

  &__map {
    display: inline-block;
    padding: 15px 10px 15px 0;
    user-select: none;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .7;
    }

  }

  &__map-img {
    position: relative;
    top: 3px;
    width: 12px;
    height: 12px;
    vertical-align: top;
  }

}

.menu-link-btn-container {

}

.menu-btn-arrow {
  display: none;
  position: relative;
  border: 0;
  background: rgba(136, 137, 148, 0.1);
  min-width: 57px;
  outline: none;
  cursor: pointer;

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -10px;
    margin-left: -10px;
    width: 20px;
    height: 20px;
    background-image: url("../images/right-arrow.svg");
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: center;
    transition: transform .2s ease-in-out;
  }

  &.toggled {

    &:after {
      transform: rotate(90deg);
    }

  }

}

.menu-sub-container {
  visibility: hidden;
  transition: .2s 0s;
  position: absolute;
  z-index: 99;
  top: 100%;
  left: 0;
  right: 0;
  padding: 0 20px;
  background: #fff;
  border-radius: 0 0 4px 4px;
}

.menu-sub {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 20px 0 20px 0;
  list-style: none;

  &__item {
    padding-left: 20px;
    padding-right: 20px;
    max-width: 400px;

    &:first-child {
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
      border-right: none;
    }

  }

  &__link {
    display: inline-block;
    margin-bottom: 20px;
    font-family: $font-family-os;
    font-size: 15px;
    font-weight: 600;
    color: #B71C1C;
    line-height: 1.3;
    text-decoration: none;
    //text-transform: uppercase;
    transition: color .2s ease-in-out;

    &:hover {
      color: rgba(183, 28, 28, 0.7);
    }

  }

  & ul {
    margin: 0;
    padding: 0;
    list-style: none;

    & > li {
      margin-bottom: 5px;

      &:last-child{
        margin-bottom: 0;
      }

      & > a {
        font-family: $font-family-os;
        font-size: 14px;
        font-weight: 400;
        color: #54555e;
        line-height: 1.3;
        text-decoration: none;
        transition: color .2s ease-in-out;

        &:hover {
          color: #B71C1C;
        }
      }

    }

  }

}

.menu-mask {
  background-color: rgba(0,0,0,.5);
  bottom: 0;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateZ(0);
  transition: opacity .2s linear;
  visibility: hidden;
  z-index: 0;

  &.toggled {
    opacity: 1;
    visibility: visible;
    z-index: 4;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

  .menu-nav {
    padding-top: 20px;
  }

  .menu {

    &__item {

    }

    &__link {
      padding: 10px 7px;
      font-size: 13px;
      font-weight: 600;
    }

    &__map {
      padding: 7px 10px 7px 0;
    }

    &__map-img {

    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .menu-nav {
    padding-top: 0;
    position: fixed;
    top: 0;
    bottom: 0;
    background: #fff;
    z-index: 99;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4,0,.2,1);
    width: 280px;

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

    &__container {
      padding: 0;
    }

  }

  .menu-nav-user {
    display: block;
    padding: 20px;

    &__link {
      display: block;
      margin-top: 10px;
      font-family: $font-family-os;
      font-size: 15px;
      font-weight: 500;
      color: #36373f;
      text-decoration: none;
      user-select: none;
      transition: color .2s ease-in-out;

      &:hover {
        color: #B71C1C;
      }
    }

  }

  .menu {
    flex-wrap: wrap;

    &__item {
      width: 100%;
      transition: 1s ease-out;
      transition-delay: 0s;
      border-bottom: 1px solid rgba(136, 137, 148, 0.5);

      &:hover > a {
        color: #B71C1C;
      }

      &:hover .menu-sub-container {
        visibility: visible;
        transition-delay: 0s;
      }

    }

    &__link {
      flex-grow: 1;
      display: block;
      padding: 20px 10px 20px 20px;
      font-size: 15px;
      font-weight: 600;
    }

    &__map {
      display: none;
      padding: 15px 10px 15px 0;
      user-select: none;
      transition: opacity .2s ease-in-out;

      &:hover {
        opacity: .7;
      }

    }

    &__map-img {
      position: relative;
      top: 3px;
      width: 12px;
      height: 12px;
      vertical-align: top;
    }

  }

  .menu-link-btn-container {
    display: flex;
  }

  .menu-btn-arrow {
    display: inline-block;
  }

  .menu-sub-container {
    padding: 0;
    visibility: visible;
    position: relative;
    top: 0;
    border-radius: 0;
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transition: all .2s ease-in-out;

    &.toggled {
      opacity: 1;
      max-height: 10000px;
    }

  }

  .menu-sub {
    justify-content: left;
    flex-direction: column;
    padding: 0;

    &__item {
      padding-left: 0;
      padding-right: 0;

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        padding-right: 0;
      }

    }

    &__link {
      flex-grow: 1;
      display: block;
      margin-bottom: 0;
      padding: 20px 10px 20px 40px;
      font-size: 15px;
      font-weight: 500;
      color: #36373f;
      text-transform: none;
    }

    & ul {
      margin: 0;
      padding: 0;
      list-style: none;
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      transition: all .2s ease-in-out;

      &.toggled {
        opacity: 1;
        max-height: 10000px;
      }

      & > li {

        & > a {
          display: inline-block;
          padding: 10px 40px 10px 60px;
        }

      }

    }

  }

}

