.social-list {
  display: flex;
  margin: 0;
  list-style: none;

  // Версия для слабовидящих
  #{$body-class} & {
    padding-left: 0;
  }

  &_footer {
    padding-left: 0;
  }

  &__item {
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    display: inline-block;
    //line-height: 1;
    transition: opacity .2s ease-in-out;

    &:hover {
      opacity: .7;
    }

  }

  &__img {
    width: 14px;
    height: 14px;
    vertical-align: bottom;

    // Версия для слабовидящих
    #{$body-class} & path {
      fill: #000;
    }

    &_footer {
      width: 25px;
      height: 25px;
    }

  }

}

@media only screen and (max-width : 1024px) {

  .social-list {

    &__img {
      width: 11px;
      height: 11px;
      vertical-align: middle;

      &_footer {
        width: 20px;
        height: 20px;
      }

    }

  }


}

