.participants-map {
  width: 100%;
  height: 400px;
}

.participants {
  margin: 0;
  padding: 0;
  list-style: none;

  & > li {
    padding: 20px 0;
    border-bottom: 1px solid rgba(136,137,148,.3);

    &:last-child {
      border-bottom: none;
    }

  }

  &__title {
    margin: 0;
    font-family: $font-family-m;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.3;
    color: #000!important;

    & > a {
      text-decoration: none;
      color: #B71C1C!important;
      transition: color .2s ease-in-out;

      &:hover {
        color: #D32F2F!important;
      }
    }

  }

  &__detail {
    margin: 10px 0 0 0;
    display: inline-block;
    font-size: 14px;
    color: #54555e;

    & > a {
      text-decoration: none;
      color: #B71C1C!important;
      transition: color .2s ease-in-out;

      &:hover {
        color: #D32F2F!important;
      }
    }

  }

}


