.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  border-collapse: collapse;


  font-family: $font-family-os!important;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: rgba(0,0,0,.87);

  th,
  td {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
  }

  thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
    text-align: left;
  }

  //tbody + tbody {
  //  border-top: (2 * $table-border-width) solid $table-border-color;
  //}

  .table {
    background-color: transparent;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .table {
    display: block;
    width: 100%;
    overflow-x: auto;

    font-size: 15px;
  }

}
