.banner-wrap {
  position: relative;
  background: url(../images/banner.jpg) no-repeat;
  background-size: cover;
  background-position: center center;
  width: 100%;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(37, 26, 45, 0.7);
  }

}

.banner {
  position: relative;
  z-index: 1;
  padding: 40px 0 40px 0;

  &__divider {
    margin: 40px 0;
    width: 100%;
    height: 3px;
    background: rgba(255,255,255,.3);
  }

  &__row {
    display: flex;
  }

  &__col-slider {
    width: 66.66%;
    padding-right: 50px;
  }

  &__col-ask {
    width: 33.33%;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .banner {
    padding: 40px 0 40px 0;

    &__divider {
      margin: 40px 0;
    }

    &__row {
      display: flex;
      flex-wrap: wrap;
    }

    &__col-slider {
      width: 100%;
      padding-right: 0;
    }

    &__col-ask {
      padding-top: 40px;
      width: 100%;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .banner-wrap {

    &:after {
      background: rgba(37, 26, 45, 0.9);
    }

  }

  .banner {
    padding: 40px 0 20px 0;

    &__divider {
      margin: 20px 0;
    }

    &__row {

    }

    &__col-slider {
      width: 100%;
    }

    &__col-ask {
      padding-top: 20px;
    }

  }

}

