@import "normalize.css";

$body-class: '.bvi';

$font-family-m: 'Montserrat', sans-serif;
$font-family-os: 'Open Sans', sans-serif;

* {
  box-sizing : border-box;
}

html {
  height: 100%;
}

body {
  //display: flex;
  //flex-direction: column;
  //flex-grow: 1;
  //flex-basis: 100%;
  //height: 100%;
  //min-height: 100vh;
  /* [1] */
  /* Ye Olde Flexbox for Webkit */
  display: -webkit-box;
  /* Tweener flexbox for IE10 */
  display: -ms-flexbox;
  /* Prefixed "new flexbox" */
  display: -webkit-flex;
  /* unprefixed standard "new flexbox" version for the rest */
  display: flex;

  /* [2] */
  /* Your grandparents flexbox for old Webkit */
  -webkit-box-orient: vertical;
  /* Prefixed new syntax: */
  -webkit-flex-direction: column;
  /* (Same syntax for weird IE tweener) */
  -ms-flex-direction: column;
  /* unprefixed new syntax */
  flex-direction: column;

  /* [3] */
  min-height: 100vh;



  font-family: $font-family-os;
  background: #F5F5F6;

  &.toggled {
    overflow: hidden!important;
  }

}

