.content-wrap {

}

.content-aside {
  padding-left: 50px;
}

.content-title {
  //margin: 0 0 20px 0;
  font-family: $font-family-m;
  font-weight: 500;
  font-size: 36px;
  line-height: 1.3;
}

.content-time {
  display: block;
  margin: 0;
  //padding-top: 20px;
  font-family: $font-family-m;
  font-size: 12px;
  font-weight: 600;
  color: #54555e;
  text-transform: uppercase;
}

.content-teaser {
  padding-bottom: 15px;
  font-family: $font-family-m;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  border-bottom: 1px solid rgba(136,137,148,.3);
}

.content-nav {

}

.content-nav-title {
  padding: 20px 20px 5px 20px;
}

.content-menu {
  margin: 0;
  padding: 0 0 15px 0;
  list-style: none;

  &__item {

  }

  &__link {
    display: block;
    padding: 5px 20px;
    font-family: $font-family-os;
    font-size: 14px;
    font-weight: 400;
    color: #54555e;
    line-height: 1.5;
    text-decoration: none;
    transition: all .2s;

    &:hover {
      color: #B71C1C;
    }

  }

}

.content {
  //color: #000!important;
  font-family: $font-family-os;

  & h1 {
    font-family: $font-family-m !important;
    font-weight: 500!important;
    //font-size: 36px!important;
    line-height: 1.3!important;
    color: #000!important;

    //& span {
    //  font-family: $font-family-m !important;
    //  font-weight: 400!important;
    //  //font-size: 36px!important;
    //  line-height: 1.3!important;
    //  color: #000!important;
    //}
    //
    //& strong {
    //  font-family: $font-family-m !important;
    //  font-weight: 400!important;
    //  //font-size: 36px!important;
    //  line-height: 1.3!important;
    //  color: #000!important;
    //}

  }

  & h2, & h3, & h4, & h5 {
    font-family: $font-family-m !important;
    font-weight: 500!important;
    line-height: 1.3!important;
    color: #000!important;
  }

  & p {
    font-family: $font-family-os!important;
    font-weight: 400!important;
    font-size: 16px!important;
    line-height: 1.5!important;
    color: rgba(0,0,0,.87);

    margin-left: 0!important;
    margin-right: 0!important;

    & span {
      font-family: $font-family-os!important;
      font-size: 16px!important;
      line-height: 1.5!important;
      color: rgba(0,0,0,.87)!important;
      text-decoration: none!important;
    }

    & strong {
      font-family: $font-family-os!important;
      font-size: 16px!important;
      line-height: 1.5!important;
      color: rgba(0,0,0,.87)!important;
    }

    //& img {
    //  width: 100%;
    //  height: auto;
    //}

  }

  & ul, & ol {
    font-family: $font-family-os!important;
    font-weight: 400!important;
    font-size: 16px!important;
    line-height: 1.5!important;
    color: rgba(0,0,0,.87)!important;
  }

  & a {
    text-decoration: none;
    color: #B71C1C!important;
    transition: color .2s ease-in-out;

    &:hover {
      color: #D32F2F!important;
    }

    & > sup {
      color: rgba(0,0,0,.87)!important;
    }

  }

  & hr {
    border: none;
    border-bottom: 1px solid rgba(136,137,148,.3);
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {

  .content-aside {
    padding-left: 40px;
  }

  .content-title {
    font-size: 30px;
  }

  .content-teaser {
    font-size: 15px;
  }

  .content {

    & h1 {
      font-size: 30px!important;
    }

    & h2, & h3, & h4, & h5 {

    }

    & p {
      font-size: 15px!important;

      & span {
        font-size: 15px!important;
      }

      & strong {
        font-size: 15px!important;
      }

    }

    & ul, & ol {
      font-size: 15px!important;
    }

  }


}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {

  .content-aside {
    padding-top: 40px;
    padding-left: 0;
  }

  .content-title {
    font-size: 26px;
  }

  .content {

    & h1 {
      font-size: 26px!important;
    }

    & h2 {
      font-size: 24px!important;
    }
    & h3 {
      font-size: 22px!important;
    }
    & h4 {
      font-size: 20px!important;
    }
    & h5 {
      font-size: 18px!important;
    }

    & p {
      font-size: 15px!important;

      & span {
        font-size: 15px!important;
      }

      & strong {
        font-size: 15px!important;
      }

    }

    & ul, & ol {
      font-size: 15px!important;
    }

  }

}

